/**
 * POST TEMPLATE
 */

/**
 * Dependencies.
 */
import React, { useContext } from 'react'
import { Link, graphql } from 'gatsby'
import { MDXProvider } from '@mdx-js/react'
import { MDXRenderer } from 'gatsby-plugin-mdx'
import { LocalesContext } from '../components/locales/LocalesContextProvider'
import LazyLoad from 'react-lazy-load'
import { DiscussionEmbed } from 'disqus-react'
import Img from 'gatsby-image'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFolder, faClock, faTags, faChevronCircleDown } from '@fortawesome/free-solid-svg-icons'
import AuthorBox from '../components/AuthorBox.jsx'
import SocialSharing from '../components/SocialSharing.jsx'
import { AnchorLink } from 'gatsby-plugin-anchor-links'
import _ from 'lodash'
import ReactEmbedGist from '../components/ReactEmbedGist'
import styled from 'styled-components'
import BackgroundImage from 'gatsby-background-image'
import { useLocation } from '@reach/router'

//import Breadcrumb from '../components/Breadcrumb2'

import '../styles/blog-post.css'

import config from'../../gatsby-config'

/**
 * FA ICON COMPONENTS.
 */
const FaChevronCircleDown = () => <FontAwesomeIcon icon={faChevronCircleDown} />
const FaFolderIcon = () => <FontAwesomeIcon icon={faFolder} />
const FaClockIcon = () => <FontAwesomeIcon icon={faClock} />
//const FaUserCircleIcon = () => <FontAwesomeIcon icon={faUserCircle} />
//const FaDateIcon = () => <FontAwesomeIcon icon={faCalendarAlt} />
const FaTagsIcon = () => <FontAwesomeIcon icon={faTags} />

/**
 * Template Component (JSX)
 */
//export default function PostTemplate({ props, data: { mdx, PostImages, SidebarImages, site } }) {
export default function PostTemplate({ data: { mdx, PostImages } }) {

  const { locales, siteUrl } = config.siteMetadata
  const { pathname } = useLocation()
  const pageUrl = `${siteUrl}${pathname}`

  /**
   * Hero Background
   */
  const BackgroundSection = ({ className }) => {
    return (
      <BackgroundImage
      id="hero"
      Tag="section"
      className={className}
      /*preserveStackingContext="false"*/
      fluid={mdx.frontmatter.featuredImage?.childImageSharp.fluid}
      /*
      backgroundColor={`var(--main-brand)`}
      fadeIn="false"
      critical="true"
      */
      >
        <div className="container">
          <div className="text">
            {/*<Breadcrumb pageMeta={mdx.frontmatter.breadcrumb} />*/}
            { mdx.frontmatter.category && <><span id="category"><FaFolderIcon /><Link to={langKey === locales.defaultLangKey ? `/${_.kebabCase(mdx.frontmatter.category)}/` : `/${langKey}/${_.kebabCase(mdx.frontmatter.category)}/`} title={`${categoryi18n}: ${mdx.frontmatter.category}`}>{mdx.frontmatter.category}</Link></span></> }
            <h1>{mdx.frontmatter.title}</h1>
            <p>{mdx.frontmatter.description}</p>
            {(authorInfos || mdx.frontmatter.date || mdx.frontmatter.tags) && <div className="metas">{ authorInfos.name && <div id="author" title={authori18n}><div id="img" style={{backgroundImage: `url(${authorInfos.photo})`}}></div><p>{authorInfos.name}</p></div> }{ mdx.frontmatter.dateFromNow && <div id="date" title={publishedDatei18n}><FaClockIcon />{mdx.frontmatter.dateFromNow}</div>}
            {/*(authorInfos || mdx.frontmatter.date || mdx.frontmatter.tags) && <ul className="metas">{ authorInfos.name && <li id="author" title={authori18n}><FaUserCircleIcon />{authorInfos.name}</li> }{ mdx.frontmatter.dateFromNow && <li id="date" title={publishedDatei18n}><FaClockIcon />{mdx.frontmatter.dateFromNow}</li> */}
            {/*(mdx.frontmatter.date || mdx.frontmatter.tags) && <ul className="metas">{ mdx.frontmatter.dateFromNow && <li id="date" title={publishedDatei18n}><FaClockIcon />{mdx.frontmatter.dateFromNow}</li>*/}
            {mdx.frontmatter.tags && <>
            <ul className="tags">
              {mdx.frontmatter.tags.map( (tag, index) => (
                <li key={index} className="tag">{index === 0 ? <FaTagsIcon /> : null }<Link to={langKey === locales.defaultLangKey ? `/tags/${_.kebabCase(tag)}/` : `/${langKey}/tags/${_.kebabCase(tag)}/`} title={`${tagi18n}: ${tag}`}>{tag}</Link></li>
              ))}
            </ul>
            </>}
            </div>
            }
          </div>
          <SocialSharing url={pageUrl}/>
        </div>
        <a id="scrollDown" href="#content" data-scroll data-easing="easeInCubic"><FaChevronCircleDown/></a>
      </BackgroundImage>
    )
  }
  const StyledBackgroundSection = styled(BackgroundSection)`
    width: 100%;
    min-height: 90vh;
    display: flex;
    /*
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    */
    background-color: var(--main-brand);
    /*background-blend-mode: luminosity;*/
    /*
    &::before {
      opacity: 0.5!important;
    }
    &::after {
      opacity: 0.25!important;
    }
    */
    /*
    &::before,
    &::after {
      transition: none!important;
    }
    &::after {
      opacity: 0.5!important;
    }
    */
  `
  /*
  const HeroCover = styled.div`
    background-image: url(${props => (props.image)};
    background-blend-mode: soft-light;
  `
  */

  // Get Post images from post directory
  let img
  const PostImagesArray = ( props ) => {
    const idx = parseInt(props.idx)
    PostImages.edges.forEach( (node, index) => {
      //PostImages.edges.map( (node, index) => {
      if( idx === index ) {
        img = <figure className="postImage" aria-label={props?.alt}><a href={props?.link || PostImages.edges[index].node.childImageSharp.fluid.src} target="_blank" rel="noreferrer"><Img fluid={PostImages.edges[index].node.childImageSharp.fluid} alt={props?.alt} style={props?.style} imgStyle={props?.imgStyle} placeholderStyle={props?.placeholderStyle} /></a><figcaption>{props?.alt}</figcaption></figure>
      }
    } )
    return img
  }

  /**
   * Sidebar (Variation 1)
   */
  /*
  const SidebarOne = () => {
    return(
      <a href="https://open.spotify.com/user/12178439438/playlist/0DWhuSF5jjRZzSrZ9DG0EV" target="_blank" rel="noreferrer">
        <Img fixed={SidebarImages.edges[0].node.childImageSharp.fixed} alt="The Best Of All Time Music Playlist On Spotify has now reached over 500 hits!"/>
      </a>
    )
  }
  */

  // Provide common components
  const shortcodes = { Link, Img, PostImagesArray, ReactEmbedGist }
  /*const shortcodes = { Link, Img, PostImagesArray, ReactEmbedGist, SidebarOne }*/

  // Get the locales context
  const { langKey, i18n } = useContext(LocalesContext)
  const i18nData = i18n.find(el => el.langKey === langKey)
  const {
    comments, 
    author, 
    category: categoryi18n,
    tag: tagi18n,
    authorLabel: authori18n,
    publishedDate: publishedDatei18n,
    toc: toci18n
  } = i18nData.locales

  // Get the site meta data.
  //const { siteUrl } = site.siteMetadata
  //const pageMeta = mdx.frontmatter.breadcrumb

  // Get the author infos from the article OR the default one locales
  const authorInfos = mdx?.frontmatter?.author || author

  // Render component.
  return (
    <>
    <StyledBackgroundSection/>
    <div id="content" className="container">
      <aside id="sidebar">
        {/*<h2>Author</h2>*/}
        <div id="toc">
          <h2>{toci18n}</h2>
          <ul>
          {mdx.frontmatter.toc.map( (toc, index) => (
            <li key={index}><AnchorLink to={toc[1]} title={toc[0]} /></li>
          ))}
          </ul>
        </div>
      </aside>
      {/*<div id="content" className={mdx ? `container ${subLayout.container}` : `container`}>*/}
      {/*<div id="content" className={`container ${mdx.frontmatter?.template}`}>*/}
      {/*<div id="content" className={subLayout.container ? `container ${subLayout.container}` : `container`}>*/}
      <MDXProvider components={shortcodes}>
        <MDXRenderer>{mdx.body}</MDXRenderer>
      </MDXProvider>
    </div>
    <div id="comments" className="container">
      <div className="content">
        <h2>{comments}</h2>
        <LazyLoad offsetTop={400}>
        <DiscussionEmbed
          shortname='guydumaisdigital'
          config={
            {
              url: siteUrl + mdx.fields.slug,
              identifier: mdx.slug,
              title: mdx.frontmatter.title,
              language: langKey
            }
          }
        />
        </LazyLoad>
      </div>
      <aside id="ads2">
        <AuthorBox author={authorInfos}/>
        {/*<SidebarOne />*/}
      </aside>
    </div>
    </>
  )

}

/**
 * GRAPHQL QUERY
 *               ...GatsbyImageSharpFluidLimitPresentationSize
 * type Frontmatter @infer {
 * title: String!
 * date: String!
 * author: String
 * published: Boolean
 * category: String!
 * description: String!
 * tags: [String!]
 * template: String!
 * toc: String
 *}
 */
export const BlogPostDataQuery = graphql`
  ###
  # Post data
  ###
  query BlogPostQuery($id: String, $langKey: String, $slug: String) {
    mdx(id: { eq: $id }) {
      id
      body
      fields {
        slug
        langKey
      }
      frontmatter {
        title
        date
        dateFromNow: date(fromNow: true, locale: $langKey)
        author {
          name
          bio
          photo
          links {
            facebook
            twitter
            linkedin
            website
          }
        }
        published
        category
        description
        tags
        template
        toc
        languageAlternates
        featuredImage {
          childImageSharp {
            fluid(
              maxWidth: 1600,
              #maxHeight: 470,
              quality: 100,
              #grayscale: true,
              duotone: { highlight: "#EA5600", shadow: "#7C2600" }
              #A33600
            ) {
              #...GatsbyImageSharpFluid_withWebp
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
              #...GatsbyImageSharpFluid_withWebp_noBase64
            }
          }
        }
      }
    }

    ###
    # Post Images
    ###
    PostImages: allFile(sort: {fields: [absolutePath], order: ASC}, filter: {extension: {regex: "/(jpg)|(jpeg)|(png)/"}, relativeDirectory: {regex: $slug}}) {
      edges {
        node {
          fields {
            slug
          }
          absolutePath
          childImageSharp {
            fluid(
              maxWidth: 840,
              #maxHeight: 470,
              quality: 100,
              srcSetBreakpoints: [360, 768],
              sizes: "(max-width: 360px) calc(100vw - 30px), (max-width: 768px) calc(100vw - 60px), 840px"
            ) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    }

    ###
    # Sidebar Images
    ###
    SidebarImages: allFile(sort: {fields: [absolutePath], order: ASC}, filter: {extension: {regex: "/(jpg)|(jpeg)|(png)/"}, relativeDirectory: {in: "sidebar"}}) {
      edges {
        node {
          fields {
            slug
          }
          absolutePath
          childImageSharp {
            fixed(
              width: 300
              quality: 100
            ) {
              ...GatsbyImageSharpFixed_withWebp_tracedSVG
            }
          }
        }
      }
    }

    ###
    # Site Meta Data
    ###
    site {
      siteMetadata {
        title
        siteLogo
        description
        siteUrl
      }
    }
  }
`
