/**
 * SOCIAL SHARING.
 */

/**
 * Dependencies.
 */
import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import { LocalesContext } from '../components/locales/LocalesContextProvider'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebook, faTwitter, faLinkedin, faReddit } from '@fortawesome/free-brands-svg-icons'
import {
  FacebookShareButton,
  //FacebookIcon,
  LinkedinShareButton,
  //LinkedinIcon,
  TwitterShareButton,
  //TwitterIcon,
  //WhatsappShareButton,
  //WhatsappIcon,
  RedditShareButton,
  //RedditIcon
} from 'react-share'

/**
 * FA ICON COMPONENTS.
 */
const FaFacebookIcon = () => <FontAwesomeIcon icon={faFacebook} />
const FaTwitterIcon = () => <FontAwesomeIcon icon={faTwitter} />
const FaLinkedInIcon = () => <FontAwesomeIcon icon={faLinkedin} />
const FaRedditIcon = () => <FontAwesomeIcon icon={faReddit} />

/**
 * React component (JSX).
 */
const SocialSharing = ( {url} ) => {

  // Get localized data.
  const { langKey, i18n } = useContext(LocalesContext)
  const i18nData = i18n.find(el => el.langKey === langKey)
  const { shareOn } = i18nData.locales

  // Render component.
  return (
  <>
  <div className="social-sharing">
    <FacebookShareButton url={url} title={`${shareOn} Facebook`}>
      <FaFacebookIcon/>
    </FacebookShareButton>
    <TwitterShareButton url={url} title={`${shareOn} Twitter`}>
      <FaTwitterIcon/>
    </TwitterShareButton>
    <LinkedinShareButton url={url} title={`${shareOn} LinkedIn`}>
      <FaLinkedInIcon/>
    </LinkedinShareButton>
    <RedditShareButton url={url} title={`${shareOn} Reddit`}>
      <FaRedditIcon/>
    </RedditShareButton>
  </div>
  </>
  )
}

/**
 * Properties.
 */
SocialSharing.propTypes = {
  url: PropTypes.string.isRequired
}

export default SocialSharing
